import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_BLUR = Symbol('TABLE_BLUR');

export function tableBlur() {
  return {
    type: TABLE_BLUR,
  };
}

interface TableBlurPayload {}

interface TableBlurAction {
  type: typeof TABLE_BLUR;
  payload: TableBlurPayload;
}

export function isTableBlurAction(
  action: Action,
): action is TableBlurAction {
  return action.type === TABLE_BLUR;
}
